.mini-card{
    height: 150px;
    width: 150px;
    background-color: blue;
    color: white;
    text-align: center;
    margin: 15px;
    padding: 10px;
    border-radius: 4px;
    font-size: 16px;
    cursor: pointer;

}

.mini-card img{
    height: 80%;
    width: 80%;
    object-fit: cover;
}