.card{
    height: 200px;
    width: 270px;
    background-color: blue;
    color: white;
    text-align: center;
    margin: 15px;
    padding: 10px;
    border-radius: 4px;
    font-size: 16px;
    cursor: pointer;
}

.card img{
    height: 60%;
    width: 90%;
    object-fit: cover;
}